.team-backdrop {
    position: fixed;
    top: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.2);
  }
  .team-modal {
    position: absolute;
    top: 50%;
    left: 50%;
    width: 280px;
    background-color: $whiteColor;
    transform: translate(-50%, -50%);
    padding: 8px 8px 20px 20px;
  
    @include tablet {
      width: 750px;
    }
  }

.team-modal-close {
    position: absolute;
    right: 16px;
    top: 8px;
    width: 30px;
    height: 30px;
    border: transparent;
    background-color: $whiteColor;
  }
  
  .team-modal-close-icon {
    stroke: $blackColor;
  }

.teammate__header {
    margin-top: 15px;
    font-size: 20px;
    text-align: center;

    @include tablet {
        font-size: 40px;
        font-weight: 500;
        margin-top: 25px;
        margin-bottom: 20px;
      }
}

.teammate__list {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: center;
    list-style: none;
    padding: 30px 10px;
}

.teammate__item {
    display: block;
    width: 80px;
    
    
    &:nth-child(2n+1) {
        padding-right: 7px;
        border-right: 1px solid #000;
      }
    &:nth-child(2n) {
        padding-left: 7px;
      }
    &:nth-child(n+3) {
        padding-top: 5px;
    }
    &:nth-child(-n+8) {
        padding-bottom: 5px;
        border-bottom: 1px solid #000;
    }

    @include tablet {
        display: flex;
        flex-direction: column;
        flex-wrap: wrap;
        width: 110px;
    &:nth-child(n) {
        border: none;
        padding: 10px;
    }
    &:not(:nth-child(5n)) {
margin-right: 30px;
    }
   
    }
}

.teammate__img {
    display: none;
    width: 100%;
    height: 100%;
    margin-left: auto;
    margin-right: auto;


    @include tablet {
     display: flex;   
     margin-bottom: 10px;

     img {
        border-radius: 50%;
        border: 2px solid rgb(140, 134, 134);
     }
    }
}

.teammate__name {
font-size: 14px;
text-align: center;
font-weight: 600;

@include tablet {
font-size: 18px;
font-weight: 500;
}
}

.teammate__role {
font-size: 10px;
margin-top: 5px;
text-align: center;
font-weight: 400;

@include tablet {
    border-top: 1px solid #000;
    padding-top: 5px;
    font-size: 14px;
    font-weight: 400;
    }
}

.teammate__link {
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 5px;
    margin-top: 5px;
    text-align: center;
    &:hover {
        .teammate__icon{
            fill: rgb(172, 17, 17);
        }
    }
}


